import {Link} from 'gatsby'
import PropTypes from 'prop-types'
import React from 'react'
import './landingPage.css'

const Header = ({ siteTitle }) => (
  <Link to="/" replace>
    {siteTitle}
  </Link>
)

Header.propTypes = {
  siteTitle: PropTypes.string,
}

Header.defaultProps = {
  siteTitle: ``,
}

export default Header
